.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  width: 100%;
  background: #fff;
  -webkit-font-smoothing: antialiased;

  .wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 10;
    background: transparent;

    .brandContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;

      .logo {
        width: 30vw;

        img {
          width: 100%;
        }
      }

      .slogan {
        text-transform: uppercase;
        font-size: 21pt;
        font-weight: 900;
        margin-top: 20pt;
      }
    }
  }

  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-position: center;
    background-size: cover;
  }
}

@media (max-width: 767px) {
	.mainContainer {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}

	.wrapper {
		.brandContainer {
      .logo {
        width: 80vw !important;
      }

      .slogan {
        font-size: 10pt !important;
      }
    }
	}
}