@import "~antd/lib/style/themes/default.less";
@import "~antd/dist/antd.less";

@import (css) url('https://fonts.googleapis.com/css2?family=Bungee&family=Lobster&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

@primary-color: #30f4ff;
@progress-default-color: @primary-color;
@border-radius-base: 16px;

html,
body,
#root {
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
}

.colorWeak {
  filter: invert(80%);
}

.ant-layout {
  min-height: 100vh;
}
.ant-pro-sider.ant-layout-sider.ant-pro-sider-fixed {
  left: unset;
}

canvas {
  display: block;
}

body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol {
  list-style: none;
}

.ant-input-affix-wrapper-disabled .ant-input[disabled],
.ant-picker-large .ant-picker-input > input[disabled] {
  color: #000 !important;
}

@media (max-width: @screen-xs) {
  .ant-table {
    width: 100%;
    overflow-x: auto;
    &-thead > tr,
    &-tbody > tr {
      > th,
      > td {
        white-space: pre;
        > span {
          display: block;
        }
      }
    }
  }
}

// Compatible with IE11
@media screen and(-ms-high-contrast: active), (-ms-high-contrast: none) {
  body .ant-design-pro > .ant-layout {
    min-height: 100vh;
  }
}
